import { createReportAsync } from '@ohif/extension-default';
import React, {useCallback, useEffect, useState} from 'react';
import PropTypes, {element} from 'prop-types';
import {Button, SegmentationGroupTable, SegmentationGroupTableExpanded, useViewportGrid, ListMenu} from '@ohif/ui';
import { SegmentationPanelMode } from '../types/segmentation';
import callInputDialog from './callInputDialog';
import callColorPickerDialog from './colorPickerDialog';
import * as csTools from '@cornerstonejs/tools';
import BaseToolEdit from "./segToolsEditSection/baseToolEdit";
import SegmentsConfigurationImportExport from "./segConfigurationIO";
import SegmentationImportExport from "./segmentationIO";
import {useTranslation} from 'react-i18next';
import { DisplaySet, RGB } from 'platform/core/src/types';
import { VolumeData } from 'extensions/cornerstone/src/types/CornerstoneCacheService';
import { viewport } from '@cornerstonejs/tools/src/utilities';


const LABELMAP = csTools.Enums.SegmentationRepresentations.Labelmap;
const components = {
  [SegmentationPanelMode.Expanded]: SegmentationGroupTableExpanded,
  [SegmentationPanelMode.Dropdown]: SegmentationGroupTable,
};

const seededRandom = (seed: number) => {
  let value = seed % 2147483647;
  return () => {
      value = (value * 16807) % 2147483647;
      return value / 2147483647;
  };
}
const myRandom = seededRandom(42);

export default function PanelSegmentation({
  servicesManager,
  commandsManager,
  extensionManager,
  configuration,
}: withAppTypes) {
  // @ts-ignore
  const {
    segmentationService,
    viewportGridService,
    uiDialogService,
    displaySetService,
    cornerstoneViewportService,
    hangingProtocolService,
    toolGroupService,
    toolbarService,
    uiNotificationService,
    panoramicService,
  } = servicesManager.services;

  const { t } = useTranslation('PanelSegmentation');

  const [addSegmentationClassName, setAddSegmentationClassName] = useState('');
  const [segmentationConfiguration, setSegmentationConfiguration] = useState(
    segmentationService.getConfiguration()
  );

  const getAxialSegmentations = () => {
    const displaySetUID = viewportGridService.getDisplaySetsUIDsForViewport('default')[0];
    const segmentations = segmentationService.getSegmentations().filter(
      segmentation => segmentation.displaySetInstanceUID === displaySetUID
    );

    return segmentations;
  }

  const getDentascanSegmentations = () => {
    const dentascanDisplaySet = displaySetService.getActiveDisplaySets().find(
      displaySet => displaySet.SeriesInstanceUID.includes('dentascan')
    );
    if (!dentascanDisplaySet) {
      return;
    }

    const dentascanSegmentations = segmentationService.getSegmentations().filter(
      segmentation => segmentation.displaySetInstanceUID === dentascanDisplaySet.displaySetInstanceUID
    );

    return dentascanSegmentations;
  }

  const isDentascanViewport = (viewportId) => {
    const viewportInfo = cornerstoneViewportService.getViewportInfo(viewportId);
    const volumeData = viewportInfo.getViewportData().data[0] as VolumeData;
    return volumeData.volume?.metadata.SeriesInstanceUID.includes('dentascan');
  }

  const isActiveViewportDentascan = isDentascanViewport(viewportGridService.getActiveViewportId());
  const [visibleSegmentations, setVisibleSegmentations] = useState(
    () => isActiveViewportDentascan ? getDentascanSegmentations() : getAxialSegmentations()
  );
  const [hiddenSegmentations, setHiddenSegmentations] = useState(
    () => isActiveViewportDentascan ? getAxialSegmentations() : getDentascanSegmentations()
  );
  const [isMinimized, setIsMinimized] = useState({});

  const onToggleMinimizeSegmentation = useCallback(
    id => {
      setIsMinimized(prevState => ({
        ...prevState,
        [id]: !prevState[id],
      }));
    },
    [setIsMinimized]
  );

  // Only expand the last segmentation added to the list and collapse the rest
  useEffect(() => {
    const lastSegmentationId = visibleSegmentations[visibleSegmentations.length - 1]?.id;
    if (lastSegmentationId) {
      setIsMinimized(prevState => ({
        ...prevState,
        [lastSegmentationId]: false,
      }));
    }
  }, [visibleSegmentations, setIsMinimized]);

  useEffect(() => {
    // ~~ Subscription
    const added = segmentationService.EVENTS.SEGMENTATION_ADDED;
    const updated = segmentationService.EVENTS.SEGMENTATION_UPDATED;
    const removed = segmentationService.EVENTS.SEGMENTATION_REMOVED;
    const subscriptions = [];

    [added, updated, removed].forEach(evt => {
      const { unsubscribe } = segmentationService.subscribe(evt, () => {
        const isActiveViewportDentascan = isDentascanViewport(viewportGridService.getActiveViewportId());
        setVisibleSegmentations(isActiveViewportDentascan ? getDentascanSegmentations() : getAxialSegmentations());
        setHiddenSegmentations(isActiveViewportDentascan ? getAxialSegmentations() : getDentascanSegmentations());
        setSegmentationConfiguration(segmentationService.getConfiguration());
      });
      subscriptions.push(unsubscribe);
    });

    return () => {
      subscriptions.forEach(unsub => {
        unsub();
      });
    };
  }, []);

  // Create dentascan segmentation when panoramic is created
  useEffect(() => {
    const evt = panoramicService.EVENTS.PANORAMIC_CREATED;
    const { unsubscribe } = panoramicService.subscribe(evt, async ({ displaySetUID }) => {
      const dentascanSegmentations = getDentascanSegmentations();
      const activeSegmentation = segmentationService.getActiveSegmentation();
      if (dentascanSegmentations?.length || !activeSegmentation) {
        return;
      }

      const panoramicState = panoramicService.getPanoramicState(displaySetUID);
      const dentascanDisplaySetUID = panoramicState.dentascanViewportData.data[0].displaySetInstanceUID;
      const toolGroupId = 'mpr';
      for (const segmentation of getAxialSegmentations()) {
        const dentascanSegId = await segmentationService.createSegmentationForDisplaySet(
          dentascanDisplaySetUID, { label: segmentation.label }
        );
        await segmentationService.addSegmentationRepresentationToToolGroup(toolGroupId, dentascanSegId, true, LABELMAP);
        for (const segment of segmentation.segments) {
          if (segment) {
            const segmentConfig = {
              segmentIndex: segment.segmentIndex,
              toolGroupId: toolGroupId,
              properties: {
                label: segment.label,
                color: segment.color,
                opacity: segment.opacity,
                visibility: segment.isVisible,
                isLocked: segment.isLocked,
                active: segmentation.activeSegmentIndex === segment.segmentIndex
              }
            }
            segmentationService.addSegment(dentascanSegId, segmentConfig);
          }
        }
      }
      // Reset active segmentation as it got modified
      segmentationService.setActiveSegmentationForToolGroup(activeSegmentation.id, toolGroupId);
    });

    return () => {
      unsubscribe();
    };
  }, []);

  // temporary measure to not allow add segmentation when the selected viewport
  // is stack viewport
  useEffect(() => {
    const handleActiveViewportChange = viewportId => {
      const activeDisplaySetsUIDs = viewportGridService.getDisplaySetsUIDsForViewport(
        viewportId || viewportGridService.getActiveViewportId()
      );

      if (!activeDisplaySetsUIDs) {
        return;
      }

      const isReconstructable =
          activeDisplaySetsUIDs?.some(displaySetUID => {
          const displaySet = displaySetService.getDisplaySetByUID(displaySetUID);
          return displaySet?.isReconstructable;
        }) || false;

      if (isReconstructable) {
        setAddSegmentationClassName('');
      } else {
        setAddSegmentationClassName('ohif-disabled');
      }

      // Only run on viewportIdChanged or viewportsReady
      if (viewportId) {
        const activeSegmentation = segmentationService.getActiveSegmentation();
        const hiddenSegmentation = getHiddenSegmentation(activeSegmentation?.id);
        // Ensure the segmentation we are moving to is hidden
        if (hiddenSegmentation && activeDisplaySetsUIDs.includes(hiddenSegmentation.displaySetInstanceUID)) {
          for (const toolGroupId of getToolGroupIds(hiddenSegmentation.id)) {
            segmentationService.setActiveSegmentationForToolGroup(hiddenSegmentation.id, toolGroupId);
          };
          // Switch visible and hidden segmentations
          const isActiveViewportDentascan = isDentascanViewport(viewportId);
          setVisibleSegmentations(isActiveViewportDentascan ? getDentascanSegmentations() : getAxialSegmentations());
          setHiddenSegmentations(isActiveViewportDentascan ? getAxialSegmentations() : getDentascanSegmentations());
        }
      }
    };

    // Handle initial state
    handleActiveViewportChange();

    const changedGrid = viewportGridService.EVENTS.ACTIVE_VIEWPORT_ID_CHANGED;
    const ready = viewportGridService.EVENTS.VIEWPORTS_READY;

    const subsGrid = [];
    [ready, changedGrid].forEach(evt => {
      const { unsubscribe } = viewportGridService.subscribe(evt, ({ viewportId }) => {
        handleActiveViewportChange(viewportId);
      });

      subsGrid.push(unsubscribe);
    });

    const changedData = cornerstoneViewportService.EVENTS.VIEWPORT_DATA_CHANGED;

    const subsData = [];
    [changedData].forEach(evt => {
      const { unsubscribe } = cornerstoneViewportService.subscribe(evt, () => {
        handleActiveViewportChange();
      });

      subsData.push(unsubscribe);
    });

    // Clean up
    return () => {
      subsGrid.forEach(unsub => unsub());
      subsData.forEach(unsub => unsub());
    };
  }, []);

  const getHiddenSegmentation = (segmentationId) => {
    const axialSegmentations = getAxialSegmentations();
    const dentascanSegmentations = getDentascanSegmentations();
    if (!dentascanSegmentations) {
      return;
    }

    const axialSegmentationIndex = axialSegmentations.findIndex(segmentation => segmentation.id === segmentationId);
    if (axialSegmentationIndex !== -1) {
      return dentascanSegmentations[axialSegmentationIndex];
    }

    const dentascanSegmentationIndex = dentascanSegmentations.findIndex(segmentation => segmentation.id === segmentationId);
    if (dentascanSegmentationIndex !== -1) {
      return axialSegmentations[dentascanSegmentationIndex];
    }
  }

  const getToolGroupIds = segmentationId => {
    const toolGroupIds = segmentationService.getToolGroupIdsWithSegmentation(segmentationId);

    return toolGroupIds;
  };

  const onSegmentationAdd = async () => {
    commandsManager.runCommand('createEmptySegmentationForViewport', {
      viewportId: viewportGridService.getActiveViewportId(),
    });
  };

  const onSegmentationClick = (segmentationId: string) => {
    for (const toolGroupId of getToolGroupIds(segmentationId)) {
      segmentationService.setActiveSegmentationForToolGroup(segmentationId, toolGroupId);
    };
  };

  const onSegmentationDelete = (segmentationId: string) => {
    // Also delete hidden segmentation
    // Delete hidden segmentation first so the new active segmentation is visible
    const hiddenSegmentation = getHiddenSegmentation(segmentationId);
    if (hiddenSegmentation) {
      segmentationService.remove(hiddenSegmentation.id);
    }

    segmentationService.remove(segmentationId);
  };

  const onSegmentClick = (segmentationId, segmentIndex) => {
    segmentationService.setActiveSegment(segmentationId, segmentIndex);

    for (const toolGroupId of getToolGroupIds(segmentationId)) {
      segmentationService.setActiveSegmentationForToolGroup(segmentationId, toolGroupId);
      segmentationService.jumpToSegmentCenter(segmentationId, segmentIndex, toolGroupId);
    };

    const hiddenSegmentation = getHiddenSegmentation(segmentationId);
    if (hiddenSegmentation) {
      segmentationService.setActiveSegment(hiddenSegmentation.id, segmentIndex);
      // Also jump for hidden segment
      for (const toolGroupId of getToolGroupIds(hiddenSegmentation.id)) {
        segmentationService.jumpToSegmentCenter(hiddenSegmentation.id, segmentIndex, toolGroupId);
      }
    }
  };

  const onSegmentEdit = (segmentationId, segmentIndex) => {
    const segmentation = segmentationService.getSegmentation(segmentationId);

    const segment = segmentation.segments[segmentIndex];
    const { label } = segment;

    callInputDialog(uiDialogService, label, (label, actionId) => {
      if (label === '') {
        return;
      }

      segmentationService.setSegmentLabel(segmentationId, segmentIndex, label);
      // Also edit hidden segment
      const hiddenSegmentation = getHiddenSegmentation(segmentationId);
      if (hiddenSegmentation) {
        segmentationService.setSegmentLabel(hiddenSegmentation.id, segmentIndex, label);
      }
    });
  };

  const onSegmentAdd = (segmentationId) => {
    const segmentIndex = segmentationService.getSegmentation(segmentationId).segments.length;

    const config = {
      segmentIndex: segmentIndex,
      properties: {
        label: `Segment ${segmentIndex}`,
        color: [Math.floor(myRandom() * 256), Math.floor(myRandom() * 256), Math.floor(myRandom() * 256)] as RGB,
        opacity: 255,
        visibility: true,
        isLocked: false,
        active: true
      }
    }

    segmentationService.addSegment(segmentationId, config);

    // Also add on hidden segmentation
    // Add also on dentascan segmentation
    const hiddenSegmentation = getHiddenSegmentation(segmentationId);
    if (hiddenSegmentation) {
      segmentationService.addSegment(hiddenSegmentation.id, config);
    }
  }

  const onSegmentationEdit = (segmentationId) => {
    const segmentation = segmentationService.getSegmentation(segmentationId);
    const { label } = segmentation;

    callInputDialog(uiDialogService, label, (label, actionId) => {
      if (label === '') {
        return;
      }

      segmentationService.addOrUpdateSegmentation(
        {
          id: segmentationId,
          label,
        },
        false, // suppress event
        true // notYetUpdatedAtSource
      );

      // Also update hidden segmentation
      const hiddenSegmentation = getHiddenSegmentation(segmentationId);
      if (hiddenSegmentation) {
        segmentationService.addOrUpdateSegmentation({ id: hiddenSegmentation.id, label }, false, true);
      }
    });
  };

  const onSegmentColorClick = (segmentationId, segmentIndex) => {
    const segmentation = segmentationService.getSegmentation(segmentationId);

    const segment = segmentation.segments[segmentIndex];
    const { color, opacity } = segment;

    const rgbaColor = {
      r: color[0],
      g: color[1],
      b: color[2],
      a: opacity / 255.0,
    };

    callColorPickerDialog(uiDialogService, rgbaColor, (newRgbaColor, actionId) => {
      if (actionId === 'cancel') {
        return;
      }

      segmentationService.setSegmentRGBAColor(segmentationId, segmentIndex, [
        newRgbaColor.r,
        newRgbaColor.g,
        newRgbaColor.b,
        newRgbaColor.a * 255.0,
      ]);

      // Also update hidden segment
      const hiddenSegmentation = getHiddenSegmentation(segmentationId);
      if (hiddenSegmentation) {
        segmentationService.setSegmentRGBAColor(hiddenSegmentation.id, segmentIndex, [
          newRgbaColor.r,
          newRgbaColor.g,
          newRgbaColor.b,
          newRgbaColor.a * 255.0,
        ]);
      }
    });
  };

  const onSegmentDelete = (segmentationId, segmentIndex) => {
    segmentationService.removeSegment(segmentationId, segmentIndex);
    // Also delete hidden segment
    const hiddenSegmentation = getHiddenSegmentation(segmentationId);
    if (hiddenSegmentation) {
      segmentationService.removeSegment(hiddenSegmentation.id, segmentIndex);
    }
  };

  // segment hide
  const onToggleSegmentVisibility = (segmentationId, segmentIndex) => {
    const segmentation = segmentationService.getSegmentation(segmentationId);
    const segmentInfo = segmentation.segments[segmentIndex];
    const isVisible = !segmentInfo.isVisible;

    // Todo: right now we apply the visibility to all tool groups
    for (const toolGroupId of getToolGroupIds(segmentationId)) {
      segmentationService.setSegmentVisibility(
        segmentationId,
        segmentIndex,
        isVisible,
        toolGroupId
      );
    };

    // Also hide hidden segment
    const hiddenSegmentation = getHiddenSegmentation(segmentationId);
    if (hiddenSegmentation) {
      for (const toolGroupId of getToolGroupIds(hiddenSegmentation.id)) {
        segmentationService.setSegmentVisibility(
          hiddenSegmentation.id,
          segmentIndex,
          isVisible,
          toolGroupId
        );
      };
    }
  };

  const onToggleSegmentLock = (segmentationId, segmentIndex) => {
    segmentationService.toggleSegmentLocked(segmentationId, segmentIndex);
    // Also lock hidden segment
    const hiddenSegmentation = getHiddenSegmentation(segmentationId);
    if (hiddenSegmentation) {
      segmentationService.toggleSegmentLocked(hiddenSegmentation.id, segmentIndex);
    }
  };

  const onToggleSegmentationVisibility = segmentationId => {
    segmentationService.toggleSegmentationVisibility(segmentationId);
    const segmentation = segmentationService.getSegmentation(segmentationId);
    const isVisible = segmentation.isVisible;
    const segments = segmentation.segments;

    const toolGroupIds = getToolGroupIds(segmentationId);

    toolGroupIds.forEach(toolGroupId => {
      segments.forEach((segment, segmentIndex) => {
        segmentationService.setSegmentVisibility(
          segmentationId,
          segmentIndex,
          isVisible,
          toolGroupId
        );
      });
    });
  };

  const _setSegmentationConfiguration = useCallback(
    (key, value) => {
      segmentationService.setConfiguration({[key]: value});
    }, [segmentationService]
  );

  const createSegmentation = async () => {
    // TODO Maybe method not relevant

    const toolGroupId = 'mpr';
    const activeDisplaySets = displaySetService.getActiveDisplaySets();
    const options = { label: `Segmentation ${visibleSegmentations.length + 1}` };
    const segmentConfig = {
      segmentIndex: 1,
      toolGroupId: toolGroupId,
      properties: {
        label: `Segment 1`,
        color: [255, 0, 0] as RGB,
        opacity: 255,
        visibility: true,
        isLocked: false,
        active: true
      }
    };

    // Create segmentation for main volume
    const axialDisplaySet = activeDisplaySets.find(displaySet => displaySet.Modality === 'CT');
    const axialDisplaySetUID = axialDisplaySet.displaySetInstanceUID;
    const segId = await segmentationService.createSegmentationForDisplaySet(axialDisplaySetUID, options);
    const segmentation = segmentationService.getSegmentation(segId);
    console.log('CREATED SEGMENTATION', segmentation);
    await segmentationService.addSegmentationRepresentationToToolGroup(
      toolGroupId,
      segId,
      true,
      LABELMAP
    );
    segmentationService.addSegment(segId, segmentConfig);

    // Create segmentation for dentascan volume
    const dentascanDisplaySet = activeDisplaySets.find(
      displaySet => displaySet.SeriesInstanceUID.includes('dentascan')
    );
    if (dentascanDisplaySet) {
      const dentascanDisplaySetUID = dentascanDisplaySet.displaySetInstanceUID;
      const dentascanSegId = await segmentationService.createSegmentationForDisplaySet(dentascanDisplaySetUID, options);
      await segmentationService.addSegmentationRepresentationToToolGroup(toolGroupId, dentascanSegId, true, LABELMAP);
      segmentationService.addSegment(dentascanSegId, segmentConfig);

      // Set active segmentation according to the current viewport
      const activeSegId = isDentascanViewport(viewportGridService.getActiveViewportId()) ? dentascanSegId : segId;
      segmentationService.setActiveSegmentationForToolGroup(activeSegId, toolGroupId);
    }

    // Add segmentation tools to toolbar
    console.log('MTOOLS', toolbarService.getButtonSection('MeasurementTools', {}));
    toolbarService.createButtonSection('primary', [
      'MeasurementTools',
      'Zoom',
      'Pan',
      'TrackballRotate',
      'WindowLevel',
      'Capture',
      'Layout',
      'MPR',
      'Panoramic',
      'Crosshairs',
      'MoreTools',
      'SegmentationTools',
    ]);
  }

  const onSegmentationDownload = segmentationId => {
    commandsManager.runCommand('downloadSegmentation', {
      segmentationId,
    });
  };

  const storeSegmentation = async segmentationId => {
    const datasources = extensionManager.getActiveDataSource();

    const displaySetInstanceUIDs = await createReportAsync({
      servicesManager,
      getReport: () =>
        commandsManager.runCommand('storeSegmentation', {
          segmentationId,
          dataSource: datasources[0],
        }),
      reportType: 'Segmentation',
    });

    // Show the exported report in the active viewport as read only (similar to SR)
    if (displaySetInstanceUIDs) {
      // clear the segmentation that we exported, similar to the storeMeasurement
      // where we remove the measurements and prompt again the user if they would like
      // to re-read the measurements in a SR read only viewport
      segmentationService.remove(segmentationId);

      viewportGridService.setDisplaySetsForViewport({
        viewportId: viewportGridService.getActiveViewportId(),
        displaySetInstanceUIDs,
      });
    }
  };

  const onSegmentationDownloadRTSS = segmentationId => {
    commandsManager.runCommand('downloadRTSS', {
      segmentationId,
    });
  };

  const SegmentationGroupTableComponent =
    components[configuration?.segmentationPanelMode] || SegmentationGroupTable;
  const allowAddSegment = configuration?.addSegment;
  const onSegmentationAddWrapper =
    configuration?.onSegmentationAdd && typeof configuration?.onSegmentationAdd === 'function'
      ? configuration?.onSegmentationAdd
      : onSegmentationAdd;

  const volume3dCheck = () => {
    const viewportInfo = cornerstoneViewportService.getViewportInfo(viewportGridService.getActiveViewportId());
    if (viewportInfo.getViewportOptions().viewportType === 'volume3d') {
      uiNotificationService.show({
        title: 'Segmentation Panel',
        message: 'Can\'t use segmentation panel in viewport 3D, select a 2D viewport',
        type: 'info',
      });
      return true;
    }

    return false;
  }

  return (
    <div className="flex flex-col flex-auto min-h-0F mt-1">
      {/* Segmentation tool edition */}
      <BaseToolEdit
          segmentationService={segmentationService}
          cornerstoneViewportService={cornerstoneViewportService}
          toolbarService={toolbarService}
      />
      {/* show segmentation table */}
      <SegmentationGroupTableComponent
        title={t('Segmentations')}
        segmentations={visibleSegmentations}
        hiddenSegmentations={hiddenSegmentations}
        segmentationConfig={{ initialConfig: segmentationConfiguration }}
        disableEditing={configuration.disableEditing}
        showAddSegment={allowAddSegment}
        showDeleteSegment={true}
        onSegmentationAdd={onSegmentationAddWrapper}
        onSegmentationEdit={onSegmentationEdit}
        onSegmentationClick={onSegmentationClick}
        onSegmentationDelete={onSegmentationDelete}
        onSegmentationDownload={onSegmentationDownload}
        onSegmentationDownloadRTSS={onSegmentationDownloadRTSS}
        storeSegmentation={storeSegmentation}
        onSegmentClick={onSegmentClick}
        onSegmentAdd={onSegmentAdd}
        onSegmentDelete={onSegmentDelete}
        onSegmentEdit={onSegmentEdit}
        onToggleSegmentationVisibility={onToggleSegmentationVisibility}
        onToggleSegmentVisibility={onToggleSegmentVisibility}
        onToggleSegmentLock={onToggleSegmentLock}
        onSegmentColorClick={onSegmentColorClick}
        setFillAlpha={value => _setSegmentationConfiguration('fillAlpha', value)}
        setFillAlphaInactive={value => _setSegmentationConfiguration('fillAlphaInactive', value)}
        setOutlineWidthActive={value => _setSegmentationConfiguration('outlineWidthActive', value)}
        setOutlineOpacityActive={value => _setSegmentationConfiguration('outlineOpacity', value)}
        setRenderFill={value => _setSegmentationConfiguration('renderFill', value)}
        setRenderInactiveSegmentations={value => _setSegmentationConfiguration('renderInactiveSegmentations', value)}
        setRenderOutline={value => _setSegmentationConfiguration('renderOutline', value)}

        addSegmentationClassName={addSegmentationClassName}
      />
      <div className="h-[6px] bg-black "></div>
      <Button
        className={'pt-1/10 pb-1/10'}
        onClick={createSegmentation}
        style={{marginTop:'10%'}}
      >{`Create segmentation`}
      </Button>
      <div className="h-[6px] bg-black "></div>
      <SegmentsConfigurationImportExport
          segmentations={visibleSegmentations}
          segmentationService={segmentationService}
      />
      <SegmentationImportExport
        segmentations={visibleSegmentations}
        segmentationService={segmentationService}
        cornerstoneViewportService={cornerstoneViewportService}
        panoramicService={panoramicService}
      />
    </div>
  );
}

PanelSegmentation.propTypes = {
  commandsManager: PropTypes.shape({
    runCommand: PropTypes.func.isRequired,
  }),
  servicesManager: PropTypes.shape({
    services: PropTypes.shape({
      segmentationService: PropTypes.shape({
        getSegmentation: PropTypes.func.isRequired,
        getSegmentations: PropTypes.func.isRequired,
        toggleSegmentationVisibility: PropTypes.func.isRequired,
        subscribe: PropTypes.func.isRequired,
        EVENTS: PropTypes.object.isRequired,
      }).isRequired,
    }).isRequired,
  }).isRequired,
};
